@include b(fly-card) {
  background-color: $background-color-white;
  box-shadow: 0 4px 5px rgba($color: #000000, $alpha: .2);
  border-radius: 6px;
  // padding: 20px;

  @include e(city) {
    // font-size: pxTorem(20px);
    font-weight: bold;
    @include box-center($justify: space-between);
    @include m(trans-left) {
      transform: translateX(100%);
      transition: all 0.25s ease-in-out;
      opacity: 0;
    }
    @include m(trans-right) {
      transform: translateX(-100%);
      transition: all 0.25s ease-in-out;
      opacity: 0;
    }
  }
  @include e(city-item) {
    flex: 1;
  }
  @include e(city-start) {
    text-align: left;
  }
  @include e(city-icon) {
    position: relative;
    width: 32px;
    @include m(circle) {
      width: 20px;
      height: 20px;      
    }
    @include m(fly) {
      width: 20px;
      height: 20px;
      @include position(absolute, top 50% left 50%);
      transform: translate(-50%, -50%)
    }
    @include m(trans) {
      transform: rotate(360deg);
      transition: all 0.5s ease-in-out;
    }
    

  }


  @include e(date) {
    @include box-center($justify: space-between)
  }
  @include e(cabin) {
    // flex: 1;
  }
  @include e(search) {

    @include m(btn) {
      background: $linearGradientThird;
      border: none !important;
      border-radius: 6px !important;
      font-size: 20px !important;
    }
  }

  @include e(tips-box) {
    @include box-center($justify: false, $align: center);
    width: 100%;
  }
  @include e(tips-item) {
    width: 25%;
    border-right: 1Px solid $border-color-primary;
    &:last-child {
      border-right: 0;
    }
  }
}

